body{
  overflow-y: hidden;
}
a{
  text-decoration: none;
  color: #0007;
}

.home{
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.home .title{
  text-align: center;
  margin-bottom: 50px;
}
.home a{
  font-size: 30px;
  padding: 15px;
  background-color: #eee;
  border-radius: 5px;
  color: #ff9800;
  box-shadow: 0 0 5px 0px #0007;
}
.vp-home{
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.vp-home a{
  font-size: 30px;
  padding: 15px;
  background-color: #eee;
  border-radius: 5px;
  color: #ff9800;
  box-shadow: 0 0 5px 0px #0007;
  margin-bottom: 20px;
}
.vp-form-wrapper{
  padding: 20px;
}
.vp-form{
  overflow-y: scroll;
  height: 100vh;
  scroll-snap-type: y mandatory;
}
.form-group{
  display: flex;
  flex-direction: column;
  font-size: 18px;
  scroll-snap-align: start;
  height: 100vh;
}
.form-group .label{
  font-weight: bold;
  margin-bottom: 40px;
}
.form-group .options-verical{
  display: flex;
  flex-direction: column;
}
.form-group .options-horizontal{
  display: flex;
  flex-direction: row;
}
.form-group .radio{
  display: flex;
  height: 50px;
}
.radio input{
  margin-right: 20px;
}
